import { useContext } from "react";
import SingleProjectContext from "../../context/SingleProjectContext";

const ProjectGallery = () => {
  const { singleProjectData } = useContext(SingleProjectContext);

  return (
    <div className="mt-12">
      <div className="mb-6 sm:mb-0" key={singleProjectData.CoverImage.id}>
        <a href={singleProjectData.CoverImage.link} target="_blank">
          <img
            src={singleProjectData.CoverImage.src}
            className="cover-img rounded-xl cursor-default shadow-lg sm:shadow-none"
            alt={singleProjectData.CoverImage.title}
            key={singleProjectData.CoverImage.id}
          />
        </a>
      </div>
    </div>
  );
};

export default ProjectGallery;
